import React from "react";
import "./ImagesPage.css";
import Carousel from "react-bootstrap/Carousel";
import backyardTable from "../Shared/backyardTable.jpeg";
import greyHouseYard from "../Shared/greyHouseYard.jpeg";
import garden from "../Shared/garden.jpg";


const ImagesPage = (props) => {


    return (
        <div className="imagesContainer">
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100 fit"
                        src={backyardTable}
                        alt="Backyard"
                    />
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100 fit"
                        src={greyHouseYard}
                        alt="Grey house yard"
                    />
                </Carousel.Item>

                <Carousel.Item>
                    <img
                        className="d-block w-100 fit"
                        src={garden}
                        alt="Garden"
                    />
                </Carousel.Item>
            </Carousel>
        </div>
    );

}

export default ImagesPage;