// /////////////////////////////////////////////////////
// INDEX GETS ALL REDUCER FILES TOGETHER IN ONE PLACE //
// Like a hub to export out everything at once       //
// /////////////////////////////////////////////////////

import CommercialReducers from "./CommercialReducers";
import ResidentialReducers from "./ResidentialReducers";
import AdminReducers from "./AdminReducers";
import { combineReducers } from "redux";

// Create a global state version- combine everything
// Defines how we want to refer to it in state (ex: query:)and how we want to use it (queryReducer)
const rootReducer = combineReducers({
    commercial: CommercialReducers,
    residential: ResidentialReducers,
    admin: AdminReducers,
});

export default rootReducer;