import React, { useState } from "react";
import { connect } from "react-redux";
import { setUser } from '../../redux/actions/AdminActions';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import "./AdminPage.css";


const AdminPage = (props) => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState("");

    async function login() { //Don't pass (username, password) because it just looks for them in local scope instead of global
        if (username.length < 8 || username.length > 20 || password.length < 8 || password.length > 20) {
            toast.warning("Username and Password must be between 8 and 20 characters.")
        }
        try {
            // Try to do the fetch to the appropriate endpoint
            // Post username and password in a body
            const response = await fetch("/login", {
                method: 'POST',
                // Headers tells it what kind of information to expect
                headers: {
                    "Content-Type": "application/json",
                },
                // Has trouble with objects so it need to be converted to a string
                body: JSON.stringify({ username: username, password: password }),
            });
            // Check the response
            const json = await response.json();
            // Handle response correctly
            if (json.error) {
                setError(json.error);
            }
            else {
                props.setUser(json.data.username);  // Refers to Redux AdminActions 
            }
        } catch (err) {
            setError("Something went wrong, please try again later.");
        }

        ///////////////////////////////////////
        // TRY AND CATCH PATTERN GOES HERE   // 
        // NEED TO GET THE INFORMATION FROM  //
        // TO THE DATABASE                   //
        ///////////////////////////////////////
    }


    ///////////////////////////////////////////////
    // CREATE A DIV TO DISPLAY RESULTS IN RETURN //
    ///////////////////////////////////////////////

    return (
        <div className="adminContainer">
            <div className="loginDiv">
                <label className="flexLabel textRight" htmlFor="username">User Name: </label>
                <input
                    className="flexTextBox"
                    type="text"
                    id="username"
                    onChange={(e) => setUsername(e.target.value)}
                    value={username} />
            </div>
            <div className="loginDiv">
                <label className="flexLabel textRight" htmlFor="password">Password: </label>
                <input
                    className="flexTextBox"
                    type="password"
                    id="password"
                    onChange={(e) => setPassword(e.target.value)}
                    value={password} />
            </div>
            <div className="textRight">
                <button onClick={() => login()}>Submit</button>
                <ToastContainer />
            </div>
        <div className="databaseContainer">
            <h3>Database Container Placeholder</h3>
            <p>{}</p>
        </div>
        </div>
    );

}


// mapDispatchToProps dispatch is telling it what redux actions you want to access
// ability to trigger those actions
const mapDispatchToProps = {
    setUser,
};

// mapStateToProps takes state and attaches them to props for keys defined.
// Give only access it needs so you don't have unnecessary re-renders
function mapStateToProps(state) {       //passing global state into the props in the component
    return {                            //function is telling it to pay attention to these states
        admin: state.admin,             // admin is the key used in reducers index to refer to ResidentalReducer
    };
}

export default connect (mapStateToProps, mapDispatchToProps) (AdminPage);
