// What actions do I need for the quote when submitted?
export const SET_QUOTE = "Set Quote";

// Build action creators functions
// Need to know the searched term so it's passed
// Functions are used in the components
export function setQuote(quote) {
    return { type: SET_QUOTE, quote: quote };
}