import React, { useState, useEffect } from "react";
import "./NewsPage.css";

const NewsPage = (props) => {
    const [error, setError] = useState("");
    const [news, setNews] = useState("");

    async function newsApp() {
        const apiKey = 'f5ce09b80b374d1bb589e3b0092400ff';
        const url = `https://newsapi.org/v2/everything?qInTitle=lawn&apiKey=${apiKey}&sortBy=relevancy&pageSize=5&language=en`;

        try {
            setError("")
            let response = await fetch(url);
            let json = await response.json();
            let resNews = json.articles;
            setNews(resNews);
        } catch (e) {
            setError("There was an error");
            setNews([]);
        }

    }
    useEffect(() => newsApp(), []);

    return (
        <div className="newsContainer">
            <h3 className="font150">Lawn Care Headlines</h3>
            <ul>
                <li><a target="_blank" rel="noreferrer" href={news[0] ? news[0].url : "loading"}>{news[0] ? news[0].title : "loading"}</a></li>
                <li><a target="_blank" rel="noreferrer" href={news[1] ? news[1].url : "loading"}>{news[1] ? news[1].title : "loading"}</a></li>
                <li><a target="_blank" rel="noreferrer" href={news[2] ? news[2].url : "loading"}>{news[2] ? news[2].title : "loading"}</a></li>
                <li><a target="_blank" rel="noreferrer" href={news[3] ? news[3].url : "loading"}>{news[3] ? news[3].title : "loading"}</a></li>
                <li><a target="_blank" rel="noreferrer" href={news[4] ? news[4].url : "loading"}>{news[4] ? news[4].title : "loading"}</a></li>
            </ul>
        </div>
    );

}

export default NewsPage;