import React, { useState, useEffect } from "react";
import "./WeatherPage.css";

const WeatherPage = (props) => {
    const [error, setError] = useState("")
    const [weather, setWeather] = useState("");

    async function weatherApp() {
        const apiKey = "da4519b8eaf5006863613f4eaff63934";
        const url = `https://api.openweathermap.org/data/2.5/onecall?lat=41.259&lon=-95.938&exclude=current,minutely,hourly,alerts&appid=${apiKey}&units=imperial`;

        try {
            setError("")
            let response = await fetch(url);
            let json = await response.json();
            let resWeather = json.daily;
            setWeather(resWeather);
        } catch (e) {
            setError("There was an error");
            setWeather([]);
        }
    }
    useEffect(() => weatherApp(), []);


    return (
        <div className="weatherContainer bold">

            <div className="weatherCard">
                <p className="day">Today</p>
                <p className="omaha font125">Omaha</p>
                <p className="maxTemp">{parseInt(weather[0] ? weather[0].temp.max : "Loading weather...")}&#730;F</p>
                <img
                    className="icon"
                    src={weather[0] ? `http://openweathermap.org/img/wn/${weather[0].weather[0].icon}@2x.png` : "Loading"}
                    alt={weather[0] ? weather[0].weather.description : "Loading"}
                />
            </div>
            <div className="weatherCard">
                <p className="day">Tomorrow</p>
                <p className="omaha font125">Omaha</p>
                    <p className="maxTemp">{parseInt(weather[1] ? weather[1].temp.max : "Loading weather...")}&#730;F</p>
                    <img
                        className="icon"
                        src={weather[1] ? `http://openweathermap.org/img/wn/${weather[0].weather[0].icon}@2x.png` : "Loading"}
                        alt={weather[1] ? weather[0].weather.description : "Loading"}
                    />
            </div>
            <div className="weatherCard">
                <p className="day">2 Days Out </p>
                <p className="omaha font125">Omaha</p>
                <p className="maxTemp">{parseInt(weather[2] ? weather[2].temp.max : "Loading weather...")}&#730;F</p>
                <img
                    className="icon"
                    src={weather[2] ? `http://openweathermap.org/img/wn/${weather[0].weather[0].icon}@2x.png` : "Loading"}
                    alt={weather[2] ? weather[0].weather.description : "Loading"}
                />
            </div>
        </div>
    );

}

export default WeatherPage;
