import React, { useState } from "react";
import { connect } from "react-redux";
import { setQuote } from '../../redux/actions/CommercialActions';
import "./CommercialPage.css";
import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";


const CommercialPage = (props) => {
    const [businessName, setBusinessName] = useState("");
    const [contactName, setContactName] = useState("");
    const [address, setAddress] = useState("");
    const [city, setCity] = useState("");
    const [state, setState] = useState("");
    const [zip, setZip] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState("");
    const [mow, setMow] = useState("");
    const [fertilize, setFertilize] = useState("");
    const [aeration, setAeration] = useState("");
    const [overseed, setOverseed] = useState("");
    const [mulch, setMulch] = useState("");
    const [cleanup, setCleanup] = useState("");
    const [snow, setSnow] = useState("");
    const [error, setError] = useState("");

    async function commercialQuote(businessName, contactName, address, city, state, zip, phone,
        email, mow, fertilize, aeration, overseed, mulch, cleanup, snow) {
        if (businessName.length < 2 || businessName.length > 20 || contactName.length < 2 || contactName.length > 20) {
            toast.info("First and last name must be between 2 and 20 characters.");
        }
        if (address.length < 8 || address.length > 30) {
            toast.warning("Address must be between 8 to 30 characters.");
        }
        if (city.length < 2 || city.length > 20) {
            toast.warning("City must be between 2 and 20 characters.");
        }
        if (state.length === 0) {
            toast.warning("Please choose your state from the drop down list.");
        }
        if (zip.length < 5) {
            toast.warning("Zip code must be 5 digits.");
        }

        function validEmail(email) {
            return /\S+@\S+\.\S+/.test(email)
        };
        validEmail(email);
        console.log(validEmail('anystring@anystring.anystring'));
        if (!validEmail) {
            toast.warning("Please provide a valid email address.")
        }

        if (phone.length < 9) {
            toast.warning("Please include area code.");
        }

        let checkboxes = [mow, fertilize, aeration, overseed, mulch, cleanup, snow];
        let checked = checkboxes.filter(v => v);
        if (checked.length < 0) {
            toast.warning("Please select a service.");
        }
        ///////////////////////////////////////////////////////////////////////
        // NEED A WAY TO MAKE SURE AT LEAST ONE CHECKBOX WAS CHECKED         //
        //                                                                   //
        // function checkboxData() {                                         //
        //     let form_data = new FormData(document.querySelector("form")); //
        //     if (!form_data.has("services[]")) {                           //
        //         toast.warning("Please include area code.")                //
        //     }                                                             //
        //                                                                   //
        //CALL CHECKBOXDATA() IN THE SUBMIT BUTTON ONCLICK?                  //
        // }                                                                 //
        ///////////////////////////////////////////////////////////////////////

        ////////////////////////////////////////////////
        // TRY AND CATCH PATTERN                      // 
        // NEED TO POST THE INFORMATION FROM THE FORM //
        // TO THE DATABASE                            //
        ////////////////////////////////////////////////
        try {
            // Try to do the fetch to the appropriate endpoint
            // Post username and password in a body
            const response = await fetch("/add", {
                method: 'POST',
                // Headers tells it what kind of information to expect
                headers: {
                    "Content-Type": "application/json",
                },
                // Has trouble with objects so it need to be converted to a string
                body: JSON.stringify({business: businessName, contact: contactName, address: address, city: city, state: state,
                zip: zip, phone: phone, email: email, mow: mow, fertilize: fertilize, aeration: aeration, overseed: overseed,
                mulch: mulch, cleanup: cleanup, snow: snow}),
            });
            // Check the response
            const json = await response.json();
            // Handle response correctly
            if (json.error){
                setError(json.error);
            }
            else {props.setQuote(json.data.quote);    // Refers to Redux CommericalActions setQuote
            }
        } catch (err) {
            setError("Something went wrong, please try again later.");
        }
    }

    return (
        <div className="commercialContainer">
            <h3 className="font150">Commerical Services</h3>
            <ul>
                <li>Snow Removal</li>
                <li>Property Mowing</li>
                <li>Fertilizer Applications</li>
                <li>Aeration</li>
                <li>Overseeding</li>
                <li>Mulch</li>
                <li>Fall Cleanup</li>
            </ul>
            <Accordion className="accordian blackFont">
                <Card>
                    <Card.Header className="submit">
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            Request Quote
      </Accordion.Toggle>
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body>
                            <form className="formContainer">
                                <div className="formDiv">
                                    <label className="flexLabel">Business Name: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setBusinessName(e.target.value)}
                                        value={businessName} />
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">Contact Name: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setContactName(e.target.value)}
                                        value={contactName} />
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">Address: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setAddress(e.target.value)}
                                        value={address} />
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">City: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setCity(e.target.value)}
                                        value={city} />
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">State: </label>
                                    <select
                                        className="flexTextbox"
                                        onChange={(e) => setState(e.target.value)}
                                        value={state}>
                                        <option></option>
                                        <option>NE</option>
                                        <option>IA</option>
                                    </select>
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">Zip Code: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setZip(e.target.value)}
                                        value={zip} />
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">Phone: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setPhone(e.target.value)}
                                        value={phone} />
                                </div>
                                <div className="formDiv">
                                    <label className="flexLabel">Email: </label>
                                    <input
                                        type="text"
                                        className="flexTextbox"
                                        onChange={(e) => setEmail(e.target.value)}
                                        value={email} />
                                </div>

                                <p>Services Requested:</p>
                                <div className="formCheckbox">
                                    <label className="flexCheckbox" htmlFor="mow">
                                        <input
                                            type="checkbox"
                                            name="services[]"
                                            id="mow"
                                            onChange={(e) => setMow(e.target.value)}
                                            value={mow} />
                                            Weekly Mow
                                    </label>
                                    <label className="flexCheckbox" htmlFor="fertilize">
                                        <input
                                            type="checkbox"
                                            name="services[]"
                                            id="fertilize" onChange={(e) => setFertilize(e.target.value)}
                                            value={fertilize} />
                                        Fertilization
                                    </label>
                                </div>
                                <div className="formCheckbox">
                                    <label className="flexCheckbox" htmlFor="aeration">
                                        <input
                                            type="checkbox"
                                            name="services[]"
                                            id="aeration" onChange={(e) => setAeration(e.target.value)}
                                            value={aeration} />
                                        Aeration
                                    </label>
                                    <label className="flexCheckbox" htmlFor="overseed">
                                        <input
                                            type="checkbox"
                                            name="services[]"
                                            id="overseed"
                                            onChange={(e) => setOverseed(e.target.value)}
                                            value={overseed} />
                                        Overseeding
                                    </label>
                                </div>
                                <div className="formCheckbox">
                                    <label className="flexCheckbox" htmlFor="mulch">
                                        <input
                                            type="checkbox"
                                            name="services[]"
                                            id="mulch"
                                            className="flexCheckbox"
                                            onChange={(e) => setMulch(e.target.value)}
                                            value={mulch} />
                                        Mulch
                                    </label>
                                    <label className="flexCheckbox" htmlFor="cleanup">
                                        <input
                                            type="checkbox"
                                            name="services[]"
                                            id="cleanup"
                                            className="flexCheckbox"
                                            onChange={(e) => setCleanup(e.target.value)}
                                            value={cleanup} />
                                        Fall Cleanup
                                    </label>
                                </div>
                                <div className="formCheckbox">
                                    <label className="flexCheckbox" htmlFor="snow">
                                        <input 
                                        type="checkbox" 
                                        name="services[]" 
                                        id="snow" 
                                        className="flexCheckbox"
                                        onChange={(e) => setSnow(e.target.value)}
                                        value={snow} />
                                        Snow Removal
                                    </label>
                                </div>
                                <div className="submit">
                                <button type="button" onClick={(e) => {
                                        e.preventDefault()
                                        commercialQuote(businessName, contactName, address, city, state, zip, phone,
                                            email, mow, fertilize, aeration, overseed, mulch, cleanup, snow)
                                    }}
                                    >Request Quote</button>
                                    <ToastContainer />
                                </div>
                            </form>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
            </Accordion>
        </div >
    );
}

// mapDispatchToProps dispatch is telling it what redux actions you want to access
// ability to trigger those actions
const mapDispatchToProps = {
    setQuote,
};

// mapStateToProps takes state and attaches them to props for keys defined.
// Give only access it needs so you don't have unnecessary re-renders
function mapStateToProps(state) {       //passing global state into the props in the component
    return {                            //function is telling it to pay attention to these states
        commercial: state.commercial, // residential is the key used in reducers index to refer to ResidentalReducer
    };
}

export default connect (mapStateToProps, mapDispatchToProps) (CommercialPage);